import React, { useEffect, useState } from 'react';
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import './CalendarPage.css';
import { useNavigate } from 'react-router-dom';

function CalendarPage() {
  const [calendarData, setCalendarData] = useState({});
  const [date, setDate] = useState(new Date(2026, 0, 1));
  const navigate = useNavigate();

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) {
      alert("You must be logged in to view the calendar.");
      navigate('/login');
      return;
    }

    fetch('http://localhost:3001/generate-calendar')
      .then(res => res.json())
      .then(data => setCalendarData(data))
      .catch(err => console.error('Error fetching calendar:', err));
  }, [navigate]);

  const stationColors = {
    DFW: '#f28b82',
    LAX: '#aecbfa',
    BCO: '#ccff90',
    SEA: '#fff475',
    ATL: '#fdcfe8',
  };

  const getTileContent = ({ date, view }) => {
    if (view !== 'month') return null;
  
    const dateStr = date.toISOString().split('T')[0];
    const dayData = calendarData[dateStr];
  
    if (!dayData) return null;
  
    return (
      <div className="vacation-day">
        {Object.entries(dayData).map(([station, people], idx) => (
          <div
            key={idx}
            className="station-block"
            style={{ backgroundColor: stationColors[station] || '#e0e0e0' }}
          >
            <strong>{station}</strong>
            <ul style={{ paddingLeft: '10px', margin: 0 }}>
              {people.map((person, i) => (
                <li key={i}>
                  {typeof person === 'object'
                    ? `${person.Name || person.Username} (P${person.Seniority})`
                    : person}
                </li>
              ))}
            </ul>
          </div>
        ))}
      </div>
    );
  };
  

  return (
    <div className="calendar-page">
      <h2 style={{ marginBottom: '20px' }}>Vacation Calendar</h2>
      <Calendar
        value={date}
        onChange={setDate}
        tileContent={getTileContent}
        minDate={new Date(2026, 0, 1)}
        maxDate={new Date(2026, 11, 31)}
      />
    </div>
  );
}

export default CalendarPage;
