import React, { useState, useEffect } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

function FormPage() {
  const [username, setUsername] = useState('');
  const [name, setName] = useState('');
  const [seniority, setSeniority] = useState('');
  const [location, setLocation] = useState('');
  const [selectedDates, setSelectedDates] = useState([]);

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) {
      alert("Please log in to use this form.");
      window.location.href = '/login';
      return;
    }

    fetch('http://localhost:3001/me', {
      headers: { Authorization: `Bearer ${token}` }
    })
      .then(res => res.json())
      .then(user => {
        setUsername(user.username);
        setName(user.name); // ✅ full name
        setLocation(user.station);
        setSeniority(user.seniority);

        return fetch('http://localhost:3001/my-vacations', {
          headers: { Authorization: `Bearer ${token}` }
        });
      })
      .then(res => res.json())
      .then(savedDates => {
        const parsed = savedDates.map(date => new Date(date));
        setSelectedDates(parsed);
      })
      .catch(() => {
        alert("Session expired. Please log in again.");
        localStorage.removeItem('token');
        window.location.href = '/login';
      });
  }, []);

  const handleSubmit = async (event) => {
    event.preventDefault();
    const token = localStorage.getItem('token');

    if (!token) {
      alert("You must be logged in.");
      return;
    }

    if (selectedDates.length === 0) {
      alert('Please select at least one vacation date.');
      return;
    }

    const requestData = {
      vacationDates: selectedDates.map(date => date.toISOString().split('T')[0])
    };

    try {
      const response = await fetch('http://localhost:3001/request-vacation', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(requestData),
      });

      const result = await response.json();

      if (!response.ok) {
        throw new Error(result.message || 'Submission failed.');
      }

      alert('Vacation request submitted!');
    } catch (error) {
      alert(`Error: ${error.message}`);
    }
  };

  const handleDateClick = (date) => {
    const exists = selectedDates.find(d => d.toDateString() === date.toDateString());

    if (exists) {
      setSelectedDates(selectedDates.filter(d => d.toDateString() !== date.toDateString()));
    } else {
      if (selectedDates.length >= 14) {
        alert("You can only select up to 14 vacation dates.");
        return;
      }
      setSelectedDates([...selectedDates, date]);
    }
  };

  const clearDates = () => {
    setSelectedDates([]);
  };

  return (
    <div>
      <h2>Vacation Request Form</h2>
      <form onSubmit={handleSubmit}>
        <div>
          <label>
            Name:
            <input type="text" value={name} readOnly />
          </label>
        </div>
        <div>
          <label>
            Username:
            <input type="text" value={username} readOnly />
          </label>
        </div>
        <div>
          <label>
            Seniority (1-3):
            <input type="number" value={seniority} readOnly />
          </label>
        </div>
        <div>
          <label>
            Station Location:
            <input type="text" value={location} readOnly />
          </label>
        </div>
        <div>
          <label>
            Select Vacation Dates:
            <DatePicker
              placeholderText="Click to select a date"
              selected={null}
              onChange={handleDateClick}
              highlightDates={selectedDates}
              minDate={new Date(2026, 0, 1)}
              maxDate={new Date(2026, 11, 31)}
              openToDate={selectedDates[selectedDates.length - 1] || new Date(2026, 0, 1)}
              showMonthDropdown
              showYearDropdown
              dropdownMode="select"
            />
          </label>
          <div>
            <strong>Selected Dates ({selectedDates.length}/14):</strong>
            <div style={{
  display: 'flex',
  flexWrap: 'wrap',
  gap: '10px',
  justifyContent: 'center', // 👈 This centers the chips
  marginTop: '10px'
}}>
  {selectedDates.map((date, index) => (
    <button key={index} type="button" onClick={() => handleDateClick(date)} style={{
      backgroundColor: '#e3e3e3',
      border: 'none',
      padding: '5px 10px',
      borderRadius: '20px',
      cursor: 'pointer'
    }}>
      {date.toDateString()} ✕
    </button>
  ))}
</div>

          </div>
          <button type="button" onClick={clearDates} style={{ marginTop: '10px' }}>
            Clear Dates
          </button>
        </div>
        <button
          type="submit"
          style={{ marginTop: '20px' }}
          title="This will replace your previously saved vacation dates."
        >
          Update Vacation Dates
        </button>
      </form>
    </div>
  );
}

export default FormPage;
