import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Link, useNavigate } from 'react-router-dom';
import FormPage from './FormPage';
import CalendarPage from './CalendarPage';
import './App.css';
import LoginPage from './LoginPage';
import SignupPage from './SignupPage';

function App() {
  const [loggedIn, setLoggedIn] = useState(false);

  useEffect(() => {
    setLoggedIn(!!localStorage.getItem('token'));
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('token');
    setLoggedIn(false);
    window.location.href = '/login';
  };

  return (
    <Router>
      <div className="App">
        <header className="App-header">
          <nav style={{ marginBottom: '20px', display: 'flex', alignItems: 'center', gap: '10px' }}>
            <Link to="/" style={{ color: 'white' }}>Vacation Form</Link>
            <Link to="/calendar" style={{ color: 'white' }}>View Calendar</Link>

            {!loggedIn && (
              <>
                <Link to="/login" style={{ color: 'white' }}>Log In</Link>
                <Link to="/signup" style={{ color: 'white' }}>Sign Up</Link>
              </>
            )}

            {loggedIn && (
              <button
                onClick={handleLogout}
                style={{
                  marginLeft: 'auto',
                  backgroundColor: '#f44336',
                  color: 'white',
                  border: 'none',
                  padding: '6px 12px',
                  borderRadius: '5px',
                  cursor: 'pointer'
                }}
              >
                Logout
              </button>
            )}
          </nav>

          <Routes>
            <Route path="/" element={<FormPage />} />
            <Route path="/calendar" element={<CalendarPage />} />
            <Route path="/login" element={<LoginPage />} />
            <Route path="/signup" element={<SignupPage />} />
          </Routes>
        </header>
      </div>
    </Router>
  );
}

export default App;
