import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

function SignupPage() {
  const [username, setUsername] = useState('');
  const [name, setName] = useState(''); // ✅ Full name
  const [password, setPassword] = useState('');
  const [station, setStation] = useState('');
  const [seniority, setSeniority] = useState('');
  const navigate = useNavigate();

  const handleSignup = async (e) => {
    e.preventDefault();

    const response = await fetch('http://localhost:3001/signup', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ username, name, password, station, seniority }) // ✅ Send full name
    });

    const result = await response.json();
    if (response.ok) {
      alert('Signup successful! Please log in.');
      navigate('/login');
    } else {
      alert(result.message || 'Signup failed.');
    }
  };

  return (
    <div className="App" style={{ padding: '20px', maxWidth: '400px', margin: '0 auto' }}>
      <h2 style={{ marginBottom: '20px' }}>Create Account</h2>
      <form onSubmit={handleSignup} style={{ display: 'flex', flexDirection: 'column', gap: '12px' }}>
        <input
          placeholder="Username"
          value={username}
          onChange={e => setUsername(e.target.value)}
          required
        />
        <input
          placeholder="Full Name"
          value={name}
          onChange={e => setName(e.target.value)}
          required
        />
        <input
          placeholder="Password"
          type="password"
          value={password}
          onChange={e => setPassword(e.target.value)}
          required
        />
        <input
          placeholder="Station"
          value={station}
          onChange={e => setStation(e.target.value)}
          required
        />
        <input
          placeholder="Seniority (1-3)"
          type="number"
          min="1"
          max="3"
          value={seniority}
          onChange={e => setSeniority(e.target.value)}
          required
        />
        <button type="submit" style={{ marginTop: '10px' }}>Sign Up</button>
      </form>
    </div>
  );
}

export default SignupPage;
